import Upsert from "../../components/Upsert";
import { FormControl } from "../../components/Upsert";
import { api } from "../../App";
import { createRef, useEffect, useRef, useState } from "react";
import { DataContext } from "../../components/RestTable";
import { useParams } from "react-router-dom";

import '@toast-ui/editor/dist/toastui-editor.css';
import '@toast-ui/editor/dist/theme/toastui-editor-dark.css';
import { Editor } from '@toast-ui/react-editor';
import 'prismjs/themes/prism-okaidia.min.css';
import '@toast-ui/editor-plugin-code-syntax-highlight/dist/toastui-editor-plugin-code-syntax-highlight.css';
import codeSyntaxHighlight from '@toast-ui/editor-plugin-code-syntax-highlight';
import Prism from 'prismjs';

import 'tui-color-picker/dist/tui-color-picker.css';
import '@toast-ui/editor-plugin-color-syntax/dist/toastui-editor-plugin-color-syntax.css';
import colorSyntax from '@toast-ui/editor-plugin-color-syntax';

const langs = [
    'java',
    'javascript',
    'python',
    'c',
    'cpp',
    'csharp',
    'go',
    'kotlin',
    'ruby',
    'rust',
    'scala',
    'swift',
    'typescript',
    'css',
    'html',
    'xml',
    'json',
    'sql',
    'shell',
    'bash',
    'powershell',
    'nginx',
    'docker',
    'yaml',
    'ini',
]

langs.forEach(lang => {
    try {
        require(`prismjs/components/prism-${lang}.min.js`);
    }
    catch (e) { }
});


export default function PagesUpsert(props) {

    const [cards, setCards] = useState([]);
    const [pageContent, setPageContent] = useState('');
    const editorRef = createRef();

    let { id } = useParams();
    id = id ? id : 0;
    const data = DataContext._currentValue.data;
    const item = data && data.find(item => item.id === parseInt(id));

    useEffect(() => {
        api.get('/cards').then(res => {
            if (res.status.toString().startsWith('2')) {
                const options = res.data.map(card => {
                    return (
                        <option key={card.id} value={card.id}>{card.title} ({card.section.title})</option>
                    )
                });
                setCards(options);
            }
        }
        ).catch(err => {
            // console.log(err);
        });
    }, []);

    function reloadEffect() {
        if (item) {
            document.getElementsByName("title")[0].value = item.title;
            document.getElementsByName("titleIcon")[0].value = item.titleIcon;
            document.getElementsByName("subtitle")[0].value = item.subtitle;
            document.getElementsByName("order")[0].value = item.order;
            api.get('/pages/' + item.id).then(res => {
                if (res.status.toString().startsWith('2')) {
                    setPageContent(res.data.content);
                    if (editorRef.current) {
                        editorRef.current.getInstance().setHTML(res.data.content);
                    }
                }
            }).catch(err => { });
            document.getElementsByName("viewCount")[0].value = item.viewCount;
            setTimeout(() => {
                document.getElementsByName("cardId")[0].value = item.card.id;
            }, 250);
        }
        else {
            document.getElementsByName("title")[0].value = '';
            document.getElementsByName("titleIcon")[0].value = '';
            document.getElementsByName("subtitle")[0].value = '';
            document.getElementsByName("order")[0].value = 0;
            setPageContent('');
            document.getElementsByName("viewCount")[0].value = 0;
            document.getElementsByName("cardId")[0].value = 1;
            if (editorRef.current) {
                editorRef.current.getInstance().setHTML('');
            }
        }
    }

    function handleMarkdownChange(e) {
        setPageContent(editorRef.current.getInstance().getHTML());
    }

    return (
        <Upsert reloadEffect={reloadEffect} title="pages">
            <input name="id" type="hidden" value={id} />
            <FormControl label="Title">
                <input name="title" type="text" className="input responsive-input input-bordered w-full" />
            </FormControl>
            <FormControl label="Title Icon">
                <input name="titleIcon" type="text" className="input responsive-input input-bordered w-full" />
            </FormControl>
            <FormControl label="Sub-Title">
                <input name="subtitle" type="text" className="input responsive-input input-bordered w-full" />
            </FormControl>
            <FormControl label="Order">
                <input name="order" type="number" className="input responsive-input input-bordered w-full" />
            </FormControl>
            <FormControl label="Views Count">
                <input name="viewCount" type="number" className="input responsive-input input-bordered w-full" />
            </FormControl>
            <FormControl label="Card">
                <select name="cardId" className="select responsive-select select-bordered w-full">
                    {cards}
                </select>
            </FormControl>
            <FormControl label="Content" className="col-span-2">
                <Editor
                    previewStyle="tab"
                    height="600px"
                    initialEditType="wysiwyg"
                    useCommandShortcut={true}
                    usageStatistics={false}
                    ref={editorRef}
                    theme="dark"
                    plugins={[
                        [codeSyntaxHighlight, { highlighter: Prism }],
                        colorSyntax
                    ]}
                    onChange={handleMarkdownChange}
                />
                <input className="input responsive-input input-bordered w-full hidden" name="content" value={pageContent} onChange={(e) => setPageContent(e.target.value)} />
            </FormControl>
        </Upsert>
    );
}